.blog-listing-item {
  display: flex;
  flex-direction: row;
  gap: 4%;
  background-color: #181818;
  border-bottom: 0.2em #585858;
  font-size: 1.5em;
  border-radius: 0.5em;
  opacity: 0;
  visibility: hidden;
  transition: opacity 1s ease-in-out, visibility 0s 1s;
}

img.title-art {
  position: absolute;
  width: auto;
  overflow: hidden;
}

.blog-listing-description {
  display: flex;
  flex-direction: column;
  gap: 0.6vh;
  padding-top: 1vh;
  padding-right: 4vh;
  width: 85vw;
  width: 85dvw;
}

.blog-tag {
  color: #BA8BAF;
  font-size: 1em;
  font-weight: bolder;

}

.blog-title {
  color: #D8D8D8;
  font-size: 1.5m;
  font-weight: bold;
}

.blog-synopsis {
  color: #D8D8D8;
  font-size: 1em;
  font-weight: lighter;
}

.blog-subscript {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
}

.blog-subscript-text {
  color: #D8D8D8;
  font-size: 0.8em;
  font-weight: lighter;
  font-style: italic;
  line-height: 3vh;
  line-height: 3dvh;
}
